
.smart-construction-dashboard {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.header {
  height: 64px;
  background: #001529;
  padding: 0 24px;
  display: flex;
  align-items: center;
}

.header h1 {
  color: white;
  margin: 0;
  font-size: 20px;
}

.content {
  flex: 1;
  position: relative;
}

.map-container {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}

.info-panel {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 2;
  width: 300px;
}

.panel-card {
  margin-bottom: 16px;
  background: rgba(255, 255, 255, 0.9);
}

.panel-card :deep(.ant-card-body) {
  padding: 12px;
}
